import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue"
export default {
	components:{
		uniNumberBox
	},
	data() {
		return {
			cartList:[],   // 购物车模拟得数据
			allChecked: false,
			totalPrice: 0   // 设置购物车总价格
		}
	},
	onLoad() {
		this.getCartData()
	},
	methods: {
		// 获取购物车数据
		async getCartData(){
			let list =await this.$api.json('cartList')
			// console.log(list)
			// map 可以改变原数组
			let cartList = list.map(item =>{
				item.check = true;
				return item
			})
			this.cartList = cartList;
			console.log(this.cartList)
			// 初始化计算总价
			this.calcTotal()
		},
		// 点击选中与不选中
		check(type,index){
			console.log(type)
			// this.allChecked = !this.allChecked
			if(type == 'item'){
				this.cartList[index].check = !this.cartList[index].check 
			}else{
				// this.allChecked = !this.allChecked
				// 设置全选与全不选
				const checked = !this.allChecked
				this.cartList.forEach(item =>{
					item.check = checked
				})
				this.allChecked = checked
			}
			this.calcTotal()
		},
		// 计算总价
		calcTotal(){
			let total = 0;
			let checked2 = true;
			// 其中一个为false，全选按钮为false
			this.cartList.forEach(item =>{
				if(item.check == true){
					let value = item.number>item.stock?item.stock:item.number
					total += item.price * value
					console.log(222)
				}else if(checked2 == true){
					checked2 = false
					console.log(111)
				}
				this.allChecked = checked2;
				console.log(checked2)
				this.totalPrice = Number(total.toFixed(2))
			})
		},
		numberChange(number,index){
			// console.log(number,index)
			this.cartList[index].number = parseInt(number);
			this.calcTotal();
			// item.number = parseInt(value)
			// this.calcTotal()
		},
		// 结算按钮
		createOrder(){
			
		}
	}
}
